import { OfControlModel, OfControlModelConfig } from '../../models';


export interface OfSwitchModelConfig extends OfControlModelConfig {
  yesText?: string;
  noText?: string;
}


export class OfSwitchModel extends OfControlModel {
  yesText: string | null;
  noText: string | null;

  constructor(config: OfSwitchModelConfig) {
    super(config);
    this.type = 'switch';
    this.yesText = config.yesText || null;
    this.noText = config.noText || null;
  }
}
