import { IOfSelectOptionDto } from './ord-combo-box-dto';
import { OfControlModel, OfControlModelConfig } from './of-control.model';
import { AppUtilityService } from '../services/app-utility-service';

export interface OfSelectBaseModelConfig extends OfControlModelConfig {
  nzMode?: 'multiple' | 'tags' | 'default';
  nzAllowClear?: boolean;
  nzMaxMultipleCount?: number;
  nzMaxTagCount?: number;
  renderOptionFunc?: (option: IOfSelectOptionDto) => string;
  renderSelectedFunc?: (option: IOfSelectOptionDto) => string;
}


export class OfSelectBaseModel extends OfControlModel {
  nzAllowClear = true;
  nzMode: 'multiple' | 'tags' | 'default' = 'default';
  nzMaxMultipleCount: number | null;
  nzMaxTagCount: number | null;
  options: IOfSelectOptionDto[] = [];
  renderOptionFunc: (option: IOfSelectOptionDto) => string;
  renderSelectedFunc: (option: IOfSelectOptionDto) => string;
  itemSelected: IOfSelectOptionDto = null;

  constructor(config: OfSelectBaseModelConfig) {
    super(config);
    this.nzMode = config?.nzMode || 'default';
    if (AppUtilityService.isNotNull(config.nzAllowClear)) {
      this.nzAllowClear = config.nzAllowClear;
    }
    if (this.nzMode === 'multiple') {
      this.nzMaxMultipleCount = config?.nzMaxMultipleCount || Number.MAX_VALUE;
    }
    this.nzMaxTagCount = config?.nzMaxTagCount || 3;
    this.placeholder = this.placeholder || '-Chọn-';
    this.renderOptionFunc = config.renderOptionFunc || null;
    this.renderSelectedFunc = config.renderSelectedFunc || null;
    if (this.value) {
      this.value = '' + this.value;
    }

  }
}
