import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OfCheckBoxModel } from './of-check-box.model';

@Component({
  selector: 'of-check-box',
  template: `
      <form [formGroup]="group">
          <label nz-checkbox [formControlName]="field.dataField">{{ field.checkBoxLabel }}</label>
      </form>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OfCheckBoxComponent implements OnInit {
  field: OfCheckBoxModel;
  group: FormGroup;

  constructor() {
  }

  ngOnInit(): void {
  }

}
