import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OfNumberModel } from './of-number.model';

@Component({
  selector: 'of-number-input',
  template: `
      <form [formGroup]="group">
          <input nz-input *ngIf="field.onlyKeyNumber;else tplNumberInput"
                 numbersOnlyInput [formControlName]="field.dataField"
                 [placeholder]="field.placeholder"
                 maxlength="{{ field.maxlength }}"
          />
          <ng-template #tplNumberInput>
              <nz-input-number [formControlName]="field.dataField"
                               [nzPlaceHolder]="field.placeholder"
                               style="width: 100%"
                               [nzMin]="field.min"
                               [nzMax]="field.max"
                               [nzStep]="field.step">
              </nz-input-number>
          </ng-template>
      </form>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OfNumberInputComponent implements OnInit {
  field: OfNumberModel;
  group: FormGroup;

  constructor() {
  }

  ngOnInit(): void {
  }

}
