import {
  ComponentFactoryResolver,
  Directive,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewContainerRef
} from '@angular/core';
import { FormGroup } from '@angular/forms';
import { DestroyRxjsService } from '../services/destroy-rxjs.service';
import { OfControlModel, OfSchemaModel } from '../models';
import { OfFieldComponent } from '../components/of-template-ref/of-field.component';
import { OfComponentRefModel } from '../components/of-component-ref/of-component-ref.model';
import { debounceTime, takeUntil } from 'rxjs/operators';
import { AppUtilityService } from '../services/app-utility-service';

@Directive({
  selector: '[ofDynamicField]',
  providers: [DestroyRxjsService]
})
export class DynamicFieldDirective implements OnInit {
  @Input() schemaModel: OfSchemaModel;
  @Input() field: OfControlModel;
  @Input() group: FormGroup;
  @Input() ofFieldTempates: OfFieldComponent[] = [];
  @Output() searchEvent = new EventEmitter<any>();
  private componentRef: any;
  private component: any;

  constructor(
    private resolver: ComponentFactoryResolver,
    private container: ViewContainerRef,
    private destroy$: DestroyRxjsService
  ) {
  }

  async ngOnInit() {
    await this.mapComponent();
    if (this.component) {
      const factory = this.resolver.resolveComponentFactory(this.component);
      this.componentRef = this.container.createComponent(factory);
      this.componentRef.instance.field = this.field;
      this.componentRef.instance.group = this.group;
      this.componentRef.instance.schemaModel = this.schemaModel;
      if (this.field.type === 'templateRef') {
        this.componentRef.instance.ofFieldTempates = this.ofFieldTempates;
      }
      if (this.schemaModel.isSearchBox && this.field.type === 'text') {
        this.componentRef.instance.searchEvent.pipe(takeUntil(this.destroy$)).subscribe(d => {
          this.searchEvent.emit(d);
        });
      }

    }
    this.init$();
  }

  async mapComponent() {
    const type = this.field.type;
    switch (type) {
      case 'checkBox':
        const { OfCheckBoxComponent } = await import('../components/of-check-box/of-check-box.component');
        this.component = OfCheckBoxComponent;
        break;
      case 'textArea':
        const { OfTextAreaComponent } = await import('../components/of-text-area/of-text-area.component');
        this.component = OfTextAreaComponent;
        break;
      case 'text':
        const { OfTextComponent } = await import('../components/of-text/of-text.component');
        this.component = OfTextComponent;
        break;
      case 'select':
        const { OfSelectComponent } = await import('../components/of-select/of-select.component');
        this.component = OfSelectComponent;
        break;
      case 'selectAsync':
        const { OfSelectAsyncComponent } = await import('../components/of-select-async/of-select-async.component');
        this.component = OfSelectAsyncComponent;
        break;
      case 'selectApi':
        const { OfSelectApiComponent } = await import('../components/of-select-api/of-select-api.component');
        this.component = OfSelectApiComponent;
        break;
      case 'selectApiCascade':
        const { OfSelectCascadeComponent } = await import('../components/of-select-cascade/of-select-cascade.component');
        this.component = OfSelectCascadeComponent;
        break;
      case 'selectSearchServer':
        const { OfSelectSearchServerComponent } = await import('../components/of-select-search-server/of-select-search-server.component');
        this.component = OfSelectSearchServerComponent;
        break;
      case 'currencyInput':
        const { OfCurrencyComponent } = await import('../components/of-currency/of-currency.component');
        this.component = OfCurrencyComponent;
        break;
      case 'datePicker':
        const { OfDatePickerComponent } = await import('../components/of-date-picker/of-date-picker.component');
        this.component = OfDatePickerComponent;
        break;
      case 'contentHtml':
        const { OfContentHtmlComponent } = await import('../components/of-content-html/of-content-html.component');
        this.component = OfContentHtmlComponent;
        break;
      case 'numberInput':
        const { OfNumberInputComponent } = await import('../components/of-number-input/of-number-input.component');
        this.component = OfNumberInputComponent;
        break;
      case 'passWordInput':
        const { OfPasswordComponent } = await import('../components/of-password/of-password.component');
        this.component = OfPasswordComponent;
        break;
      case 'radio':
        const { OfRadioComponent } = await import('../components/of-radio/of-radio.component');
        this.component = OfRadioComponent;
        break;
      case 'switch':
        const { OfSwitchComponent } = await import('../components/of-switch/of-switch.component');
        this.component = OfSwitchComponent;
        break;
      case 'componentRef':
        const field = this.field as OfComponentRefModel<any>;
        this.component = field.componentRef;
        break;
      case 'templateRef':
        const { OfTemplateRefComponent } = await import('../components/of-template-ref/of-template-ref.component');
        this.component = OfTemplateRefComponent;
        break;
    }

  }

  get f() {
    return this.group.get(this.field.dataField) || null;
  }

  private init$() {
    if (this.field.hiddenAsync) {
      this.field.hiddenAsync.pipe(takeUntil(this.destroy$))
        .pipe(debounceTime(300))
        .subscribe(d => {
          this.field.hidden = d;
        });
    }
    if (this.field.disabledAsync) {
      this.field.disabledAsync.pipe(takeUntil(this.destroy$))
        .subscribe(d => {
          this.field.disabled = d;
          if (this.f) {
            if (d) {
              this.f.disable({ onlySelf: true });
            } else {
              this.f.enable({ onlySelf: true });
            }
          }
          this.disableExtendControl(d);
        });
    }
  }

  private disableExtendControl(d) {
    if (!AppUtilityService.isNotAnyItem(this.field.controls)) {
      this.field.controls.forEach(ctrl => {
        ctrl.disabled = d;
        const fControl = this.group.get(ctrl.dataField);
        if (fControl) {
          if (d) {
            fControl.disable({ onlySelf: true });
          } else {
            fControl.enable({ onlySelf: true });
          }
        }
      });
    }
  }
}
