import { Component, OnInit, TemplateRef } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OfFieldComponent } from './of-field.component';
import { OfTemplateRefModel } from './of-template-ref.model';
import { OfCreateControlFormService } from '../../services/of-create-control-form-service';

@Component({
  selector: 'of-template-ref',
  template: `
      <ng-container [ngTemplateOutlet]="templateRef"
                    [ngTemplateOutletContext]="{ $implicit: { value: id }, group: group,field: field }"></ng-container>
      <span *ngIf="!templateRef" class="text-danger">Chưa có tempate</span>
  `
})
export class OfTemplateRefComponent implements OnInit {
  ofFieldTempates: OfFieldComponent[] = [];
  field: OfTemplateRefModel;
  group: FormGroup;
  id = Number(new Date());
  templateRef: TemplateRef<any>;

  constructor(private formService: OfCreateControlFormService) {

  }


  ngOnInit(): void {
    this.formService.createExtendControl(this.group, this.field.controls);
    this.templateRef = this.ofFieldTempates.find(x => x.id === this.field.dataField)?.controlTemplate;

  }
}
