import { Pipe, PipeTransform } from '@angular/core';
import * as _ from 'lodash';
import { OrdValidator } from '../models';

@Pipe({
  name: 'showValidationError'
})
export class ShowValidationErrorPipe implements PipeTransform {

  transform(errors: any, validations: OrdValidator[], submitted: boolean): string {
    if (!submitted) {
      return '';
    }
    let err = '';
    if (errors && validations?.length > 0) {
      _.forEach(validations, valid => {
        if (errors[valid.name]) {
          err = valid.message;
          return false;
        }
      });
    }
    return err;
  }

}
