import { OrdValidator } from './of-control.model';


export interface OfExtendControlModelConfig {
  label?: string;
  dataField: string;
  validations?: OrdValidator[];
  value?: any;
  required?: boolean;
  disabled?: boolean;
  errorEmpty?: string;
}

// cấu hình control trong template
export class OfExtendControlModel {
  label: string;
  dataField: string;
  validations?: OrdValidator[];
  value?: any;
  required?: boolean;
  disabled = false;
  errorEmpty = '';

  constructor(config: OfExtendControlModelConfig) {
    this.label = config.label || '';
    this.dataField = config.dataField;
    this.validations = config.validations || [];
    this.value = config.value;
    this.required = config.required;
    this.errorEmpty = config.errorEmpty || 'Không để trống trường này';
    this.disabled = config?.disabled;
  }
}
