import { Component } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OfSchemaModel } from '../../models';
import { OfTextAreaModel } from './of-text-area.model';

@Component({
  selector: 'of-text-area',
  template: `
      <form [formGroup]="group">
          <textarea nz-input
                    [formControlName]="field.dataField"
                    [placeholder]="field.placeholder"
                    [rows]="field.rows"
                    maxlength="{{field.maxLength}}"></textarea>
      </form>
  `
})
export class OfTextAreaComponent {
  schemaModel: OfSchemaModel;
  field: OfTextAreaModel;
  group: FormGroup;
}
