import { AbstractControl, FormGroup } from '@angular/forms';
import * as _ from 'lodash';
import { ChangeDetectorRef } from '@angular/core';
import { OfControlModel } from './of-control.model';
import { Observable, Subject } from 'rxjs';
import { debounceTime, filter, takeUntil } from 'rxjs/operators';


export interface OfSchemaModelConfig {
  layout?: 'horizontal' | 'vertical';
  fields: OfControlModel[];
  /// focus vào control đầu tiên khi init view
  focusFisrtInit?: boolean;
  errorNotValid?: string;
  isSearchBox?: boolean;
}


export class OfSchemaModel {

  id: number;
  layout?: 'horizontal' | 'vertical' = 'vertical';
  fields: OfControlModel[];
  form: FormGroup;
  focusFisrtInit = true;
  errorNotValid = 'Vui lòng điền đầy đủ thông tin!';
  private backUpDisables = {};
  private render$ = new Subject<number>();
  isSearchBox = false;
  searchBtnBusy = false;
  searchEvent$ = new Subject<any>();
  rebuilder$ = new Subject<number>();
  submitted = false;

  constructor(config: OfSchemaModelConfig) {
    this.fields = config.fields;
    this.id = Number(new Date());
    this.focusFisrtInit = config?.focusFisrtInit || true;
    this.errorNotValid = config?.errorNotValid || 'Vui lòng điền đầy đủ thông tin!';
    this.isSearchBox = config?.isSearchBox || false;

  }

  get value() {
    return this.form?.getRawValue() || null;
  }

  get valueValid() {
    if (this.form?.valid) {
      return this.value;
    }
    return null;
  }

  getField(name: string) {
    return this.fields?.find(x => x.dataField === name) || null;
  }

  getFormControl(name: string): AbstractControl | null {
    return this.form.get(name);
  }

  disableField(name: string, f = true) {
    setTimeout(() => {
      const ctr = this.getFormControl(name);
      if (ctr) {
        if (f) {
          ctr.disable({ onlySelf: true });
        } else {
          ctr.enable({ onlySelf: true });
        }
      }
      // tslint:disable-next-line:no-shadowed-variable
      const field = this.fields.find((f) => f.dataField === name);
      if (field) {
        field.disabled = f;
      }
    });

  }

  disableAll(f = true) {
    if (f) {
      this.backUpDisables = {};
      _.forEach(this.fields, field => {
        if (field.disabled) {
          this.backUpDisables[field.dataField] = true;
        }
        field.disabled = true;
        const ctr = this.getFormControl(field.dataField);
        if (ctr) {
          ctr.disable({ onlySelf: true });
        }
      });
      this.triggerRender();
      return;
    }
    _.forEach(this.fields, field => {
      const ctr = this.getFormControl(field.dataField);
      if (ctr) {
        const backUp = this.backUpDisables[field.dataField];
        if (backUp) {
          ctr.disable({ onlySelf: true });
          field.disabled = true;
        } else {
          ctr.enable({ onlySelf: true });
          field.disabled = false;
        }
      }
    });
    this.triggerRender();

  }

  hiddenFields(fields: string[]) {
    _.forEach(fields, field => {
      this.getField(field).hidden = true;
    });

  }

  showFields(fields: string[]) {
    _.forEach(fields, field => {
      this.getField(field).hidden = false;
    });

  }

  setHidden(fields: string[], hiddens: boolean[]) {
    _.forEach(fields, (field, idx) => {
      this.getField(field).hidden = hiddens[idx];
    });
  }

  setShow(fields: string[], shows: boolean[]) {
    _.forEach(fields, (field, idx) => {
      this.getField(field).hidden = !shows[idx];
    });
  }

  fieldValueChanges(name: string, time = 100) {
    return this.form?.get(name).valueChanges.pipe(debounceTime(time));
  }

  valueChanges(time = 100) {
    return this.form?.valueChanges.pipe(debounceTime(time));
  }

  patchValue(value: any) {
    Object.keys(value).forEach(name => {
      const f = this.form.get(name);
      if (f) {
        f.patchValue(value[name]);
      }
    });
    this.triggerRender();
  }

  triggerRender() {
    setTimeout(() => {
      this.render$.next(Number(new Date()));
    }, 500);
  }

  subRender(cdr: ChangeDetectorRef, destroy$: Observable<any>) {
    this.render$.pipe(filter(s => s > 0)).pipe(debounceTime(111))
      .pipe(takeUntil(destroy$))
      .subscribe(d => {
        cdr.detectChanges();
      });
  }

  addControls(controls: OfControlModel[], indexBegin: number = null) {
    if (indexBegin === null) {
      this.fields = _.concat(this.fields, controls);
    } else {
      this.fields = _.flatMap(this.fields, (value, index) => {
        if (index === indexBegin) {
          return [...controls, value];
        }
        return value;
      });
    }
    this.rebuilder();
  }

  rebuilder() {
    setTimeout(() => {
      this.rebuilder$.next(Number(new Date()));
    });
  }

  search() {
    if (this.searchEvent$) {
      this.searchEvent$.next(this.value);
    }
  }
}
