import { AfterViewInit, ChangeDetectionStrategy, Component, ViewChild } from '@angular/core';
import { OfSchemaModel } from '../../models';
import { FormGroup } from '@angular/forms';
import { OfSelectAsyncModel } from './of-select-async-model';
import { DestroyRxjsService } from '../../services/destroy-rxjs.service';
import { takeUntil } from 'rxjs/operators';
import { OfSelectComponent } from '../of-select/of-select.component';

@Component({
  selector: 'select-async',
  template: `
      <of-select #vcSelect [schemaModel]="schemaModel" [group]="group" [field]="field"></of-select>`,
  providers: [DestroyRxjsService],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OfSelectAsyncComponent implements AfterViewInit {
  @ViewChild('vcSelect') vcSelect: OfSelectComponent;
  schemaModel: OfSchemaModel;
  field: OfSelectAsyncModel;
  group: FormGroup;

  constructor(private destroy$: DestroyRxjsService) {
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.field.optionsAsync.pipe(takeUntil(this.destroy$)).subscribe(opt => {
        this.field.options = opt;
        if (this.vcSelect) {
          this.vcSelect.setOptionsView(opt);
        }
      });
    });
  }

}
