import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';

import { FormBuilder, FormGroup } from '@angular/forms';
import * as _ from 'lodash';
import { DestroyRxjsService } from '../../services/destroy-rxjs.service';
import { OfControlModel, OfSchemaModel } from '../../models';
import { OfFieldComponent } from '../of-template-ref/of-field.component';
import { OfCreateControlFormService } from '../../services/of-create-control-form-service';
import { OfValidatorService } from '../../services/of-validator.service';
import { distinctUntilChanged, filter, takeUntil } from 'rxjs/operators';

@Component({
  selector: 'of',
  templateUrl: './of-dynamic.component.html',
  styleUrls: ['./of-dynamic.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [DestroyRxjsService]
})
export class OfDynamicComponent implements OnInit, AfterViewInit {
  @Input() schemaModel: OfSchemaModel;
  @Output() submitValueEvent = new EventEmitter<any>();
  @Output() searchEvent = new EventEmitter<any>();
  form: FormGroup;
  fields: OfControlModel[] = [];
  ofFieldTempates: OfFieldComponent[] = [];

  get value() {
    return this.form?.getRawValue();
  }

  constructor(private fb: FormBuilder,
              private createCtrlService: OfCreateControlFormService,
              private validatorService: OfValidatorService,
              private destroy$: DestroyRxjsService) {
  }

  ngOnInit(): void {
    this.fields = this.schemaModel.fields || [];
    this.form = this.createCtrlService.createControl(this.fields);
    this.schemaModel.form = this.form;
    this.init$();
  }

  reBuilderForm() {
    this.fields = this.schemaModel.fields || [];
    this.createCtrlService.updateControl(this.fields, this.form);
    this.schemaModel.form = this.form;
  }


  init$() {
    this.schemaModel.rebuilder$.pipe(takeUntil(this.destroy$),
      distinctUntilChanged(),
      filter(x => x > 0))
      .subscribe(() => {
        this.reBuilderForm();
      });
  }

  trackByField(index: number, field: OfControlModel) {
    return field.dataField;
  }

  onSubmit(isCheckValid = true): any {
    this.schemaModel.submitted = true;
    if (!isCheckValid) {
      this.submitValueEvent.emit(this.value);
      return this.value;
    }
    _.forEach(this.fields, (field) => {
      const f = this.form.controls[field.dataField];
      if (f) {
        if (field.hidden) {
          f.setValidators(null);
        } else {
          f.setValidators(this.createCtrlService.bindValidations(field));
        }
      }
    });
    if (this.form.valid) {
      this.submitValueEvent.emit(this.value);
      return this.value;
    } else {
      setTimeout(() => {
        this.validatorService.focusControlItem(this.schemaModel.id);
      }, 500);
      return null;
    }
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.validatorService.focusFirst(this.schemaModel.id);
    }, 500);
  }

  onClickSearchBtn() {
    this.schemaModel.searchBtnBusy = true;
    this.searchEvent.emit(this.value);
    this.schemaModel.searchEvent$.next(this.value);
  }


  disableAll(f = true) {
    this.schemaModel.disableAll(f);
  }

  disableField(name: string, f = true) {
    this.schemaModel.disableField(name, f);
  }

  addOfFieldTempates(d: OfFieldComponent) {
    this.ofFieldTempates.push(d);
  }
}
