import { Injectable } from '@angular/core';
import * as _ from 'lodash';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { OfControlModel, OfExtendControlModel, OfType } from '../models';
import { OfValidatorService } from './of-validator.service';

@Injectable({
  providedIn: 'root'
})
export class OfCreateControlFormService {

  constructor(private fb: FormBuilder,
              private validatorService: OfValidatorService) {
  }

  createControl(fields: OfControlModel[]): FormGroup {
    const group = this.fb.group({});
    _.forEach(fields, (field: OfControlModel) => {
      this.createField(field, group);
    });
    return group;
  }

  createExtendControl(group: FormGroup, controls: OfExtendControlModel[]) {
    _.forEach(controls, (field: OfExtendControlModel) => {
      const f = group.get(field.dataField);
      if (f) {
        return;
      }
      this.createValidations(field);
      const control = this.fb.control(
        field.value,
        this.bindValidations(field)
      );
      if (field.disabled) {
        control.disable({ onlySelf: true });
      }
      group.addControl(field.dataField, control);
    });

  }

  getDataFieldAndNameConstrols(fields: OfControlModel[]) {
    const ret = [];
    _.forEach(fields, (f: OfControlModel) => {
      ret.push(f.dataField);
      if (f?.controls) {
        _.forEach(f.controls, ctrl => {
          ret.push(ctrl.dataField);
        });
      }
    });
    return ret;
  }

  updateControl(fields: OfControlModel[], group: FormGroup): void {
    const lstNameField = this.getDataFieldAndNameConstrols(fields);
    Object.keys(group.controls).forEach(key => {
      const fDataField = lstNameField.indexOf(key) > -1;
      if (!fDataField) {
        group.removeControl(key);
      }
    });
    _.forEach(fields, (field: OfControlModel) => {
      const f = group.get(field.dataField);
      if (f) {
        return;
      }
      this.createField(field, group);
    });
  }

  private createField(field: OfControlModel, group: FormGroup) {
    const ignoreType: OfType[] = ['contentHtml', 'componentRef', 'templateRef'];
    if (ignoreType.indexOf(field.type) > 0) {
      return;
    }
    this.createValidations(field);
    const control = this.fb.control(
      field.value,
      this.bindValidations(field)
    );
    if (field.disabled) {
      control.disable({ onlySelf: true });
    }
    group.addControl(field.dataField, control);
  }


  createValidations(field: OfControlModel | OfExtendControlModel) {
    field.validations = field.validations || [];
    field.validations.forEach(valid => {
      if (valid.name === 'email') {
        valid.validator = this.validatorService.emailValidator;
        valid.message = valid.message || 'Email sai định dạng!';
        return;
      }
      if (valid.name === 'phone') {
        valid.validator = this.validatorService.phoneValidator;
        valid.message = valid.message || 'Số điện thoại không đúng định dạng!';
        return;
      }
      if (valid.name === 'password') {
        valid.validator = this.validatorService.passwordValidator;
        valid.message = valid.message || 'Mật khẩu tối thiểu 8 ký tự, bao gồm chữ in hoa/ thường , số và ký tự đặc biệt!';
        return;
      }
    });
    if (field.required) {
      field.validations.push({
        name: 'whiteSpace',
        validator: this.validatorService.noWhitespaceValidator,
        message: field.errorEmpty
      });
    }
  }

  bindValidations(field: OfControlModel | OfExtendControlModel) {
    const validations = field.validations || [];
    if (validations.length > 0) {
      const validList = [];
      validations.forEach(valid => {
        validList.push(valid.validator);
      });
      return Validators.compose(validList);
    }
    return null;
  }
}
