import { OfType } from './of-type';
import { Observable } from 'rxjs';
import { OfExtendControlModel } from './of-extend-control.model';

export interface OrdValidator {
  name: string | 'email' | 'password' | 'phone';
  validator?: any;
  message?: string;
}

export interface OfControlModelConfig {
  dataField: string;
  label: string;
  hiddenLabel?: boolean;
  width?: number;
  // class css riêng cho control
  css?: string;
  required?: boolean;
  errorEmpty?: string;
  placeholder?: string;
  disabled?: boolean;
  disabledAsync?: Observable<boolean>;
  hidden?: boolean;
  hiddenAsync?: Observable<boolean>;
  validations?: OrdValidator[];
  value?: any | 'sessionTinh' | 'sessionHuyen' | 'sessionXa' | 'sessionBenhVienId';
  // thêm ghi chú cuối control nếu có
  bottomHtml?: string;
}


export abstract class OfControlModel {
  type: OfType = 'text';
  dataField = '';
  label = '';
  hiddenLabel = false;
  width = 6;
  css = '';
  required = false;
  errorEmpty = 'Không để trống trường này!';
  placeholder = '';
  disabled = false;
  disabledAsync: Observable<boolean>;
  hidden = false;
  hiddenAsync: Observable<boolean>;
  validations: OrdValidator[] = [];
  value: any | null | 'sessionTinh' | 'sessionHuyen' | 'sessionXa' | 'sessionBenhVienId';
  bottomHtml: string;
  controls: OfExtendControlModel[] = [];

  protected constructor(config: OfControlModelConfig) {
    this.dataField = config.dataField;
    this.label = config.label || this.label;
    this.hiddenLabel = config?.hiddenLabel || false;
    this.width = config.width || this.width;
    this.css = `ord-dynamic-input gutter-row ord-form-control field-${this.dataField} ${config.css || ''} `;
    this.required = config.required || this.required;
    this.errorEmpty = config.errorEmpty || this.errorEmpty;
    this.placeholder = config.placeholder || this.placeholder;
    this.disabled = config.disabled || this.disabled;
    this.disabledAsync = config.disabledAsync || null;
    this.hidden = config.hidden || this.hidden;
    this.hiddenAsync = config.hiddenAsync || null;
    this.validations = config.validations || [];
    this.value = config?.value || null;
    this.bottomHtml = config?.bottomHtml || null;
  }

}
