import { OfControlModel, OfControlModelConfig } from '../../models';


export interface OfCurrencyModelConfig extends OfControlModelConfig {
  prefix?: string;
  suffix?: string;
  precision?: number;
}


export class OfCurrencyModel extends OfControlModel {
  prefix: string | null;
  suffix: string | null;
  precision: number | null;

  constructor(config: OfCurrencyModelConfig) {
    super(config);
    this.type = 'currencyInput';
    this.prefix = config.prefix || ' ';
    this.suffix = config.suffix || ' VNĐ';
    this.precision = config.precision || 0;
  }
}
