import { OfControlModel, OfControlModelConfig } from '../../models';


export interface OrdTextModelConfig extends OfControlModelConfig {
  maxLength?: number;
  minLength?: number;
}


export class OfTextModel extends OfControlModel {
  maxLength?: number | null;
  minLength?: number | null;

  constructor(config: OrdTextModelConfig) {
    super(config);
    this.type = 'text';
    this.maxLength = config?.maxLength || null;
    this.minLength = config?.minLength || null;
  }
}
