import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OfSchemaModel } from '../../models';
import { OfPwdModel } from './of-pwd.model';

@Component({
  selector: 'of-password',
  template: `
      <form [formGroup]="group">
          <nz-input-group nzPrefixIcon="lock" [nzSuffix]="suffixTemplate">
              <input [type]="passwordVisible ? 'text' : 'password'" nz-input placeholder="{{field.placeholder}}"
                     [formControlName]="field.dataField"/>
          </nz-input-group>
          <ng-template #suffixTemplate>
              <i nz-icon [nzType]="passwordVisible ? 'eye-invisible' : 'eye'"
                 (click)="passwordVisible = !passwordVisible"></i>
          </ng-template>
      </form>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OfPasswordComponent implements OnInit {
  schemaModel: OfSchemaModel;
  field: OfPwdModel;
  group: FormGroup;

  passwordVisible = false;

  constructor() {
  }

  ngOnInit(): void {
    this.field.placeholder = this.field.placeholder || 'Nhập mật khẩu';
  }

}
