import { OfControlModel, OfControlModelConfig } from '../../models';

export interface OfNumberModelConfig extends OfControlModelConfig {
  min?: number;
  max?: number;
  step?: number;
  // input chỉ nhập số vd:sđt
  onlyKeyNumber?: boolean;
  maxlength?: number;
}


export class OfNumberModel extends OfControlModel {
  min: number | null;
  max: number | null;
  step = 1;
  onlyKeyNumber = false;
  maxlength: number | null;

  constructor(config: OfNumberModelConfig) {
    super(config);
    this.type = 'numberInput';
    this.min = config.min || null;
    this.max = config.max || null;
    this.step = config.step || 1;
    this.onlyKeyNumber = config.onlyKeyNumber || false;
    this.maxlength = config.maxlength || null;
  }
}
