import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OfSwitchModel } from './of-switch.model';

@Component({
  selector: 'of-switch',
  template: `
      <form [formGroup]="group">
          <nz-switch [formControlName]="field.dataField"
                     [nzCheckedChildren]="field.yesText ? field.yesText : checkedTemplate"
                     [nzUnCheckedChildren]="field.noText ? field.noText : unCheckedTemplate"
                     [nzDisabled]="field.disabled"
          >
              <ng-template #checkedTemplate><i nz-icon nzType="check"></i></ng-template>
              <ng-template #unCheckedTemplate><i nz-icon nzType="close"></i></ng-template>
          </nz-switch>
      </form>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OfSwitchComponent implements OnInit {
  field: OfSwitchModel;
  group: FormGroup;

  constructor() {
  }

  ngOnInit(): void {
  }

}
