import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OfCurrencyModel } from './of-currency.model';

@Component({
  selector: 'of-currency',
  template: `
      <form [formGroup]="group">
          <input
                  currencyMask
                  nz-input
                  [formControlName]="field.dataField"
                  class="ord-dynamic-input"
                  [disabled]="field.disabled"
                  [placeholder]="field.placeholder"
                  [options]="field"
          />
      </form>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  styles: [`
      input::placeholder {
          text-align: left;
      }
  `]
})
export class OfCurrencyComponent implements OnInit {
  field: OfCurrencyModel;
  group: FormGroup;
  options: any = {};

  constructor() {
  }

  ngOnInit(): void {
    this.options = {
      prefix: this.field.prefix,
      suffix: this.field.suffix,
      thousands: '.',
      decimal: ',',
      precision: this.field.precision
    };
  }

}
