import { NgModule } from '@angular/core';
import { NZ_DATE_LOCALE, NZ_I18N, vi_VN } from 'ng-zorro-antd/i18n';
import { NZ_ICONS } from 'ng-zorro-antd/icon';
import { vi as viDate } from 'date-fns/locale';
import { registerLocaleData } from '@angular/common';
import vi from '@angular/common/locales/vi';
import { AntIconService } from './ant-icon.service';
import { NgZorroAntdModule } from 'ng-zorro-antd';

registerLocaleData(vi);

@NgModule({
  declarations: [],
  exports: [NgZorroAntdModule],
  providers: [
    { provide: NZ_I18N, useValue: vi_VN },
    { provide: NZ_ICONS, useValue: new AntIconService().icons() },
    { provide: NZ_DATE_LOCALE, useValue: viDate }
  ]
})
export class AntDesignModule {
}
