import { Pipe, PipeTransform } from '@angular/core';
import { OfSelectBaseModel } from '../models/of-select-base.model';
import { AppUtilityService } from '../services/app-utility-service';

@Pipe({
  name: 'ofOptionSelected',
  pure: false
})
export class OfOptionSelectedPipe implements PipeTransform {

  transform(value: any, label: string, field: OfSelectBaseModel): string {
    if (AppUtilityService.isNotNull(value)) {
      if (typeof field.renderSelectedFunc === 'function') {
        if (field?.itemSelected) {
          return field.renderSelectedFunc(field?.itemSelected);
        }
        return '';
      } else {
        return label;
      }
    }
    return null;
  }

}
