import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OfSchemaModel } from '../../models';
import { OfRadioModel } from './of-radio.model';

@Component({
  selector: 'of-radio',
  template: `
      <form [formGroup]="group">
          <nz-radio-group [formControlName]="field.dataField"
                          [nzDisabled]="field.disabled"
                          style="width: 100%">
              <label *ngFor="let op of field.items" nz-radio [nzValue]="op.value">{{ op.label }}</label>
          </nz-radio-group>
      </form>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OfRadioComponent implements OnInit {
  schemaModel: OfSchemaModel;
  field: OfRadioModel;
  group: FormGroup;

  constructor() {
  }

  ngOnInit(): void {
  }

}
